import { IonButton, IonContent,IonCard, IonHeader, IonLoading, IonPage, IonTitle, IonToolbar, IonItem, IonIcon, IonCardContent, IonLabel } from '@ionic/react';
import { pin, wifi, wine, warning, walk } from 'ionicons/icons';
import ExploreContainer from '../../components/ExploreContainer';
import './style.css';
import {displayCurrentUser} from '../../firebaseConfig'
import { useSelector } from 'react-redux';
import {logoutUser} from '../../firebaseConfig'
import { useHistory } from 'react-router';
import React, { useEffect, useState } from 'react';
import names from '../../wordlist'
import './style.css'


const Dashboard: React.FC = () => {
  const [busy,setBusy] = useState(false)
  const username = useSelector((state:any) => state.user.username)
  const history = useHistory()
  async function logout(){
    setBusy(true)
    await logoutUser()
    setBusy(false)
    history.replace('/')
  }
  const final:any = []
  const [data, setData] = useState<any | null>(null)
  // let listofngo: any[] = []
  const [ngodata, setNgodata] = useState(false);
  useEffect(() => {

    if(!ngodata){
      ngo_data()
    }

  },[]);

  const ngo_data = async() =>{
    const datango = await displayCurrentUser("ngo")
    // console.log(datango)
    if (datango){
      setNgodata(true)
      setData(datango)
      // listofngo.push(...datango.map(obj => copy(obj)));
    }
    
  }

  
  console.log(data)
  if (data){
    for (let items of data){
      // console.log(items,"ABCD")
      final.push(
        <IonContent>
          <IonItem>
          <IonLabel>
            Ngo Name: {items.ngo_name}
          </IonLabel>
        </IonItem>

        <IonItem>
          <IonLabel>
            Username: {items.username}
          </IonLabel>
        </IonItem>


        <IonItem>
          <IonLabel>
            Cause: {items.cause}
          </IonLabel>
        </IonItem>


        <IonItem>
          <IonLabel>
            Contact: {items.contact}
          </IonLabel>
        </IonItem>

        <IonItem>
          <IonLabel>
            Address: {items.address}, {items.city}, {items.state}, {items.zip}
          </IonLabel>
        </IonItem>


        </IonContent>
      )
  
    }}



  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle>Dashboard</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent className="ion-padding">
        <IonLoading message= "Logging Out.." duration={0} isOpen={busy}/>
        
        
        <p>Hi, {ngodata}  <br></br>Welcome to NGO Dashboard</p>
        {/* <ExploreContainer /> */}
        
        {final}
        {/* {names.map(name => (
          <span className="names">{name}</span>
        ))} */}

        <IonButton onClick={logout}> Logout</IonButton>
      </IonContent>
    </IonPage>
  );
};

export default Dashboard;
